.header {
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 46px;
    padding-left: 15px;
    padding-right: 15px;
    @media (max-width: 1440px) {
        max-width: 1140px;
    }
    @media (max-width: 1200px) {
        max-width: 960px;
    }
    &-item{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__tel{
        text-decoration: none;
        color:$gray;
        transition: 0.3s ease;
        font-weight: 500;
        letter-spacing: 0.075rem;
        &:hover{
            color: $orange;
        }
    }
}
.header-cart {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    margin-left: 80px;
    @media (max-width: 1440px) {
        margin-left: 40px;
    }
    &__button{
        width: 2.5rem;
        height: 2.5rem;
        background-color: $orange;
        border: none;
        border-radius: 100%;
        background-image: url(../../../img/cart.png);
        background-size: 1rem 0.875rem;
        background-repeat: no-repeat;
        background-position: center center;
    }
    &__count{
        position: absolute;
        top: 6px;
        right: 9px;
        font-size: 0.5rem;
        color: $white;
    }
}
.logo {
    display: block;
    background-repeat: no-repeat;
    background-image: url(../../../img/logo.svg);
    width: 120px;
    height: 32px;
    margin-right: 120px;
    background-size: contain;
    @media (max-width: 1350px) {
        margin-right: 50px;
    }
    @media (max-width: 1200px) {
        margin-right: 32px;
    }
}
.nav {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    &-item{
        margin-right: 48px;
        @media (max-width: 1350px) {
            margin-right: 30px;
        }
    }
    &-link{
        text-decoration: none;
        color:$gray;
        transition: 0.3s ease;
        &:hover{
            color: $orange;
        }
    }
}