.footer {
    border-top: 1px solid $gray;
    padding-top: 77px;
    padding-bottom: 60px;
    .logo {
        width: 179px;
        height: 48px;
    }
}
.box-footer {
    display: flex;
    justify-content: space-between;
    &-item {
        display: flex;
        justify-content: space-between;
        flex: 0 0 60%;
    }
}
.box-footer-social {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.box-copyright {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}
.box-footer-item {
    display: flex;
    justify-content: space-between;
    flex: 0 0 66.4%;
}
.box-footer-list {
    font-size: 1.12rem;
    list-style-type: none;
}
.box-footer-list__item {
    margin-bottom: 1.62rem;
}
.box-footer-list__link {
    color: #27300f;
    text-decoration: none;
}
.box-footer-list__tel {
    font-size: 1.68rem;
    margin-bottom: 2.06rem;
}
.box-footer-social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
}
.box-footer-social__vk {
    background-image: url(../../../img/vk.svg);
    background-size: 23px 18px;
}
.box-footer-social__link {
    width: 48px;
    height: 48px;
    border: 2px solid #27300f;
    border-radius: 100%;
    background-repeat: no-repeat;
    display: block;
    background-position: center center;
    opacity: 0.8;
    transition: 0.2s ease;
}
.box-copyright {
    display: flex;
    justify-content: space-between;
    margin-top: 42px;
}
.box-copyright__copy {
    opacity: 0.6;
}
.box-copyright__text {
    color: #a6a8a4;
    position: relative;
}
.box-copyright__link {
    text-decoration: none;
    color: #27300f;
    opacity: 0.6;
    transition: 0.2s ease;
}