// Переменные, миксины, функции
@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";

// CSS-файлы библиотек
@import "vendor/libs";

// Основное
@import "base/fonts";
@import "base/general";

// БЭМ-блоки
@import "../blocks/modules";

:root {
    font-size: 16px;
}
body {
    font-family: 'Jost', sans-serif;
    color: $gray;
    overflow-x: hidden;
}
.title {
    font-family: 'Cormorant', serif;
    font-size: 5rem;
    span{
        font-style: italic;
    }
}
.btn{
    color: $white;
    background-color: $orange;
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.3rem 2.68rem;
    border-radius: 2rem;
    letter-spacing: 0.075rem;
    box-shadow: 0px 10px 11.18px 1.82px rgba(207,92,13,0.2);
    transition: 0.2s ease;
    &:hover{
        background-color: $orangeHover;
    }
}
.ml{
    margin-left: 7.6rem;
}
.mr{
    margin-right: 7.6rem;
}
.wrapper{
    max-width: 1440px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    @media (max-width: 1440px) {
        max-width: 1140px;
    }
    @media (max-width: 1200px) {
        max-width: 960px;
    }
}
.section-main{
    background-image: url(../img/background\ 2\ sreen.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 208px;
    position: relative;
    &__image{
        position: absolute;
    }
    &__tomato{
        right: 121px;
        bottom: 140px;
        @media (max-width: 1700px) {
            right: 10px;
        }
    }
    &__beans{
        left: 0;
        bottom: 175px;
        @media (max-width: 1700px) {
            display: none;
        }
    }
    &__avocado{
        right: -25px;
        top: 46px;
        @media (max-width: 1700px) {
            right: -61px;
            top: 101px;
            width: 129px;
        }
    }
    &__pepper{
        left: 40px;
        top: 40%;
        @media (max-width: 1700px) {
            left: -1px;
            top: 50%;
        }
    }
    &__cucumber{
        top: 0;
        left: 36%;
    }
}
.main{
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-text{
        flex: 0 0 40%;
        @media (max-width: 1350px) {
            flex: 0 0 45%;
        }
        &__description{
            font-size: 1.12rem;
            line-height: 1.51;
            margin-top: 60px;
            margin-bottom: 77px;
        }
    }
    &-visual{
        border: 1px solid rgba($gray, $alpha);
        border-radius: 100%;
        padding: 53px;
        position: relative;
        &__image{
            border: 1px solid rgba($gray, $alpha);
            border-radius: 100%;
            padding: 21px;
            img{
                border-radius: 100%;
                box-shadow: 15px 48px 52px 0px rgba($gray, $alpha);
                @media (max-width: 1350px) {
                    width: 427px;
                }
            }
        }
        &__btn{
            background-color: transparent;
            border: none;
            color: rgba($gray, 0.8);
            text-align: left;
            position: absolute;
            span{
                color: rgba($gray, 1);
                display: block;
            }
            &::before{
                content: '';
                display: block;
                width: 11px;
                height: 11px;
                border-radius: 100%;
                background-color: $gray;
                box-shadow: 0 0 0 4px #fff, 0 0 0 5px $gray;
                position: absolute;
            }
            &:focus{
                outline: none;
                &::before{
                    background-color: $orange;
                    box-shadow: 0 0 0 4px #fff, 0 0 0 5px $orange;
                }
            }
        }
        &__01{
            left: 58px;
            top: 45px;
            @media (max-width: 1350px) {
                left: 79px;
                top: 56px;
            }
            &::before{
                left: 91px;
                top: 22px;
            }
        }
        &__02{
            right: -7px;
            top: 193px;
            @media (max-width: 1350px) {
                right: -38px;
            }
            &::before{
                right: 98px;
                top: 18px;
            }
        }
        &__03{
            right: 235px;
            bottom: -49px;
            @media (max-width: 1350px) {
                bottom: -63px;
            }
            &::before{
                left: 19px;
                top: -24px;
            }
        }
    }
}
.section-stock{
    background-image: url(../img/background\ 2\ sreen.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 160px;
    &__text{
        font-size: 1.5rem;
        line-height: 1.5;
        max-width: 690px;
        margin-top: 3.75rem;
    }
}
.slider{
    margin-top: 92px;
    &-child{
        padding: 5px 10px;
        box-sizing: border-box;
        &:focus{
            outline: none;
        }
    }
    &-item{
        border: 1px solid rgba($gray, $alpha);
        border-radius: 20px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        min-height: 520px;
        position: relative;
        padding: 2rem 2.25rem;
        box-sizing: border-box;
        box-shadow: 0 0 0 2px rgba($gray, 0);
        transition: 0.3s ease;
        overflow: hidden;
        &::before{
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 20px;
            transition: 0.3s ease; 
        }
        &:hover::before{
            background-color: rgba($white, 0.55);
        }
        &:hover{
            box-shadow: 0 0 0 2px rgba($gray, $alpha);
        }
        &:hover{
            .slider-text{
                transform: translate(0, 0);
            }
        }
    }
    &-milk{
        background-image: url(../img/stock/milk.png);
    }
    &-grocery{
        background-image: url(../img/stock/grocery.png);
    }
    &-meat{
        background-image: url(../img/stock/meat.png);
    }
    &-text{
        position: absolute;
        bottom: 2.75rem;
        left: 2.25rem;
        max-width: 58%;
        transform: translate(0, 241px);
        transition: 0.3s ease;
        @media (max-width: 1350px) {
            max-width: 80%;
            transform: translate(0, 255px);
        }
        &__title{
            font-size: 1.875rem;
            font-family: 'Cormorant', serif;
            margin-bottom: 26px;
        }
        &__description{
            color: rgba($gray, 0.7);
            margin-bottom: 41px;
            line-height: 1.5;
        }
        &__link{
            color: $gray;
            text-transform: uppercase;
            text-decoration: none;
            font-weight: 500;
            letter-spacing: 0.075rem;
            opacity: 0.8;
            position: relative;
            padding: 50px;
            transition: 0.3s ease;
            &:hover{
                opacity: 1;
                padding: 45px;
            }
            &:focus{
                outline: none;
            }
            &::before{
                content: '';
                position: absolute;
                left: 0;
                top: calc(50% - 0.93rem);
                display: block;
                width: 1.875rem;
                height: 1.875rem;
                background-image: url(../img/plus.svg);
            }
        }
    }
    &-thumb {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 314px;
        margin-bottom: 40px;
        &__image {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }
}

.slider-info {
    &__title {
        font-size: 1.875rem;
        font-family: 'Cormorant', serif;
        margin-bottom: 26px;
        @media (max-width: 1350px) {
            min-height: 68px;
        }
    }
    &__description {
        font-size: 1.12rem;
        color: rgba($gray, 0.7);
        line-height: 1.5;
        margin-bottom: 0;
        min-height: 52px;
        max-width: 96%;
    }
    &__price {
        font-size: 1.5rem;
        margin-top: 28px;
    }
}
.slider-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
    margin-bottom: 8px;
    &__btn {
        padding: 1.4rem 3.2rem;
        cursor: pointer;
        @media (max-width: 1350px) {
            padding: 1rem 1.8rem;
        }
    }
}
.slider-count {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    &__minus, &__plus {
        display: block;
        width: 3.12rem;
        height: 3.12rem;
        background-repeat: no-repeat;
        background-size: cover;
        cursor: pointer;
        opacity: 0.4;
        transition: 0.2s ease;
        &:hover {
            opacity: 1;
        }
        @media (max-width: 1350px) {
            width: 2.5rem;
            height: 2.5rem;
        }
    }
    &__number {
        border: none;
        width: 40px;
        text-align: center;
        @media (max-width: 1350px) {
            width: 30px;
        }
    }
    &__minus {
        background-image: url(../img/minus.svg);
    }
    &__plus {
        background-image: url(../img/plus.svg);
    }
}
.slick-prev, .slick-next{
    position: absolute;
    background-color: transparent;
    border: none;
    top: -63px;
    text-indent: -9999px;
    background-repeat: no-repeat;
    background-size: contain;
    display: block;
    width: 10px;
    height: 18px;
    opacity: 0.4;
    transition: 0.2s ease;
    cursor: pointer;
    &:hover{
        opacity: 1;
    }
    &:focus{
        outline: none;
    }
}
.slick-prev{
    right: 80px;
    background-image: url(../img/prev.svg);
}
.slick-next{
    right: 0;
    background-image: url(../img/next.svg);
}
.section-advantage{
    background-image: url(../img/background\ 3\ screen.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 208px;
}
.box-advantage{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 65px;
    &-item{
        flex: 0 0 49%;
        @media (max-width: 1350px) {
            flex: 0 0 50%;
        }
    }
    &__text{
        font-size: 1.12rem;
        line-height: 1.5;
    }
}
.box-advantage-list {
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
    padding-left: 0;
    margin: 0;
    text-align: center;
    font-family: 'Cormorant', serif;
    font-size: 1.87rem;
    font-style: italic;
    &__item{
        margin-left: 85px;
        @media (max-width: 1440px) {
            margin-left: 55px;
        }
        span{
            display: block;
            width: 87px;
            height: 87px;
            border: 1px solid $gray;
            border-radius: 100%;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 0 auto 0.9rem;
        }
    }
    &__health{
        span{
            background-size: 43px 42px;
            background-image: url('data:img/png;base64,');
        }
    }
    &__traditions{
        span{
            background-size: 40px 40px;
            background-image: url('data:img/png;base64,');
        }
    }
    &__taste{
        span{
            background-size: 45px 40px;
            background-image: url('data:img/png;base64,');
        }
    }
}
.section-catalog{
    background-image: url(../img/background\ 5\ screen.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 160px;
    &__text{
        font-size: 1.5rem;
        line-height: 1.5;
        margin-top: 3.75rem;
        letter-spacing: 1px;
    }
}
.tab {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    list-style-type: none;
    flex-wrap: wrap;
    margin-top: 87px;
    margin-bottom: 28px;
    &-item {
        max-width: 230px;
        flex-basis: 230px;
        margin-right: 20px;
        margin-bottom: 20px;
    }
    &-link {
        color: rgba($gray, 0.5);
        border: 1px solid rgba($gray, 0.5);
        border-radius: 40px;
        text-decoration: none;
        padding: 1.5rem 0;
        text-align: center;
        font-size: 1.12rem;
        display: block;
        transition: 0.2s ease;
        &:hover {
            color: $orange;
            border: 1px solid $orange;
        }
    }
}
.catalog {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    &-item{
        border: 1px solid rgba($gray, $alpha);
        border-radius: 20px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        min-height: 515px;
        position: relative;
        padding: 2rem 1.9rem;
        box-sizing: border-box;
        box-shadow: 0 0 0 2px rgba($gray, 0);
        transition: 0.3s ease;
        overflow: hidden;
        flex: 0 0 24%;
        margin-bottom: 20px;
        &:hover{
            box-shadow: 0 0 0 2px rgba($gray, $alpha);
        }
        &:hover{
            .slider-text{
                transform: translate(0, 0);
            }
        }
    }
    &-thumb {
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 204px;
        margin-bottom: 14px;
        &__image {
            position: absolute;
            left: 50%;
            top: 50%;
            height: 204px;
            transform: translate(-50%, -50%);
            object-fit: cover;
        }
    }
}
.catalog-info {
    &__title {
        font-size: 1.875rem;
        font-family: 'Cormorant', serif;
        margin-bottom: 17px;
    }
    &__description {
        color: rgba($gray, 0.7);
        line-height: 1.5;
        margin-bottom: 0;
        min-height: 52px;
        max-width: 96%;
        @media (max-width: 1440px) {
            min-height: 80px;
        }
    }
    &__price {
        font-size: 1.5rem;
        margin-top: 8px;
    }
}
.catalog-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
    margin-bottom: 8px;
    &__btn {
        padding: 1.3rem 2rem;
        cursor: pointer;
        font-size: 0.87rem;
        @media (max-width: 1440px) {
            display: block;
            width: 100%;
        }
    }
}
.catalog-count {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    @media (max-width: 1440px) {
        width: 100%;
        margin-bottom: 15px;
    }
    &__minus, &__plus {
        display: block;
        width: 2.5rem;
        height: 2.5rem;
        background-repeat: no-repeat;
        background-size: cover;
        cursor: pointer;
        opacity: 0.4;
        transition: 0.2s ease;
        &:hover {
            opacity: 1;
        }
    }
    &__number {
        border: none;
        width: 30px;
        text-align: center;
    }
    &__minus {
        background-image: url(../img/minus.svg);
    }
    &__plus {
        background-image: url(../img/plus.svg);
    }
}

.btn-load {
    background-color: transparent;
    border: none;
    text-transform: uppercase;
    position: relative;
    opacity: 0.8;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    transition: 0.2s ease;
    padding-left: 2.43rem;
    letter-spacing: 0.06rem;
    &::before {
        content: '';
        display: block;
        width: 1.56rem;
        height: 1.56rem;
        background-image: url(../img/refresh.png);
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        left: 0;
        top: calc(50% - 0.78rem);
    }
    &:hover {
        opacity: 1;
    }   
    &:focus {
        outline: none;
    }
}
.box-delivery{
    display: flex;
    justify-content: space-between;
    margin-top: 65px;
    &-item{
        flex: 0 0 49%;
        &:last-child {
            flex: 0 0 41%;
        }
    }
    &__text{
        font-size: 1.12rem;
        line-height: 1.5;
        margin-bottom: 28px;
    }
    &__link {
        color: $gray;
        border-bottom: 1px solid $gray;
        text-decoration: none;
    }
    &__span {
        color: $orange;
    }
}
.box-delivery-list {
    list-style-type: none;
    display: flex;
    margin-top: 46px;
    padding-bottom: 100px;
    &__item {
        display: block;
        background-repeat: no-repeat;
        height: 32px;
        margin-right: 1.93rem;
    }
    &__mc {
        width: 53px;
        background-image: url(../img/mastercard.svg);
        background-size: 53px 32px;
    }
    &__visa {
        width: 74px;
        background-image: url(../img/visa.svg);
        background-size: 74px 32px;
        background-position: 0 -2px;
    }
    &__paypal {
        width: 87px;
        background-image: url(../img/paypal.svg);
        background-size: 87px 24px;
        background-position: 0 4px;
    }
}
.section-form {
    background-image: url(../img/background\ end.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin-top: 160px;
}
.box-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 160px;
    .title {
        margin-bottom: 60px;
    }
    &__text {
        font-size: 1.12rem;
        line-height: 1.5;
        width: 440px;
    }
    &-item {
        flex: 0 0 39%;
        &:last-child {
            flex: 0 0 41%;
        }
    }
}
.box-form-list {
    list-style-type: none;
    margin-top: 62px;
    &__item {
        position: relative;
        padding-left: 58px;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
        &::before {
            content: '';
            display: block;
            width: 29px;
            height: 25px;
            background-repeat: no-repeat;
            position: absolute;
            left: 0;
            top: calc(50% - 12.5px);
        }
    }
    &__tel {
        &::before {
            background-image: url(data:img/png;base64,);
        }
    }
    &__email {
        &::before {
            background-image: url(data:img/png;base64,);
            background-position: 0 center;
        }
    }
    &__link {
        color: $gray;
        text-decoration: none;
        font-size: 1.5rem;
        font-weight: 500;
    }  
}
.form {
    border: 2px solid rgba($gray, $alpha);
    border-radius: 20px;
    padding: 2rem 3.5rem 1.8rem 3.5rem;
    &__h3 {
        font-family: 'Cormorant', serif;
        font-size: 1.87rem;
        margin-bottom: 31px;
    }
    &__label {
        display: block;
        font-size: 0.75rem;
        margin-bottom: 10px;
        margin-left: 5px;
    }
    &__input {
        display: block;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 1px solid rgba($gray, $alpha);
        border-radius: 20px;
        padding: 1.4rem 1.9rem;
        margin-bottom: 29px;
        transition: 0.2s ease;
        &:focus {
            outline: none;
            border: 1px solid $gray;
        }
    }
    &__text {
        font-size: 0.75rem;
        opacity: 0.6;
        margin-top: 20px;
    }
    &__link {
        color: $gray;
        border-bottom: 1px solid rgba($gray, 0.6);
        text-decoration: none;
    }
    .btn {
        margin-top: 11px;
        padding: 1.5rem 3.2rem;
    }
}
